<template>
    <div class="hotRoute">
        <div class="box">
            <div class="titleBar">
                <span class="span"></span>
                <p>热门路线</p>
                <router-link to="/hotline"><a>查看更多<img src="../../assets/images/gt.png"></a></router-link>   
            </div>
            <div class="content">
                <div class="mui-row">
                    <div class="mui-col-xs-6 pad" v-for="data in hotRoutes">
                    <router-link :to="'/detail/'+data.id">
                        <div class="liBox">
                            <div class="liImg"><img :src="imgWeb+data.coverImg"></div>
                            <div class="fontContent">
                                <p class="fontTitle">{{data.title}}</p>
                                <div class="sale">
                                <p class="fontPrice">￥{{data.price}}<span class="fontQi">起</span></p>
                                <p class="fontSale">月销<span class="fontSaleNum">30</span></p>
                                </div>
                            </div>
                        </div>
                    </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            hotRoutes:"",
            imgWeb:"http://m.whalebus.cn:8006",
            city:""
        }
    },
    created() {
        axios.get("/api/api-whale/route/app/hotTripLine",{
            address:this.city
        })
        .then((data=>{
            console.log(data.data.data)
            this.hotRoutes=data.data.data
            let storage=window.localStorage
            this.city=storage.getItem("city")
        }))
    },
}
</script>
<style scoped>
.hotRoute{
    margin-top:10px;
    background:#FFFFFF;
    width:100%;
    float:left;
}
.box{
    width:100%;
    padding:0 3px;
    /* border:1px solid red; */
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.titleBar{
    width:100%;
    height:40px;
    padding-left:7px;
    /* border:1px solid yellow; */
    /* position:relative; */
    float:left;
}
.titleBar .span{
    display:block;
    width:2px;
    height:15px;
    border-radius:10px;
    background:#2695FE;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%)
    
}
.titleBar p{
    float:left;
    font-size:16px;
    height:40px;
    line-height:40px;
    margin-left:10px;
    color:#2F99FC;
}
.titleBar a{
    float:right;
    color:#BFBFBF;
    font-weight:300;
    font-size:14px;
    height:40px;
    line-height:40px;
}
.titleBar a img{
    float:right;
    width:30px;
    position:relative;
    top:50%;
    transform:translateY(-50%)
}
.content{
    width:100%;
    /* border:1px solid green; */
    float:left;
}
.pad{
    padding: 7px;
}
.liBox{
    width:100%;
    /* padding:10px; */
    height:220px;
    /* border:1px solid blue; */
    float:left;
    border-radius:12px;
    overflow:hidden;
    box-shadow: 1px 1px 1px #F7F7F7;
    
}
.liImg{
    width:100%;
    height:150px;
    overflow:hidden;
    /* border:1px solid black; */
}
.liImg img{
    width:100%;
    /* height:100%; */
    position:relative;

}
.fontContent{
    width:100%;
    
    /* border:1px solid green; */
    float:left;
}
.fontTitle{
    width:100%;
    font-size:16px;
    font-weight:500;
    height:30px;
    line-height:30px;
    
    color:#333333;
    /* border:1px solid blue; */
    float:left;
    padding:7px  10px 0;
}
.sale{
    width:100%;
    height:40px;
    margin-top:-10px;
    /* border:1px solid red; */
    float:left;
    line-height:40px;
    padding:0 10px;
}
.fontPrice{
    color:#FF2901;
    font-size:18px;
    float:left;
    font-weight:600;
   
}
.fontQi{
    display:inline-block;
    color:#FE4C2A;
    font-size:12px;
    font-weight:200;
}
.fontSale{
    float:right;
    font-size:12px;
}
.fontSaleNum{
    display:inline-block;
    font-size:12px;
    color:gray;

}
</style>


