<template>
    <div class="my">
        <div class="content">
            <div class="header">
                <div class="headerOne">
                    <router-link to="/setting">
                        <div class="headerOneSet"><img src="../assets/images/my/set.png"></div>
                    </router-link>
                    <div class="headerOneMes"><img src="../assets/images/my/mes.png"></div>
                </div>
                <div class="headerCon">
                    <router-link to="/iconedit">
                        <div class="headerIcon">
                            <img src="../assets/images/my/icon.png">
                        </div>
                        <router-link to="/login"><p class="fontLogin" v-show="!isLogin">登录</p></router-link>
                    </router-link>
                </div>
            </div>
            <div class="dingdan">
                <div class="mui-row">
                    <div class="mui-col-xs-12">
                        <li class="liother">
                            <p style="font-size:18px;font-weight:500;color:#333333;">我的订单</p>
                            <div class="next"><img src="../assets/images/my/next.png"></div>
                        </li>
                    </div>
                </div>
            </div>
            <div class="mid">
                <div class="mui-row">
                    <div class="mui-col-xs-3" v-for="order in orders">
                        <router-link :to="{path:order.path}">
                            <li class="order">
                                <div class="orderCon">
                                    <img v-bind:src='order.img'>
                                </div>
                                <p>{{order.title}}</p>
                            </li>
                        </router-link>
                    </div>
                </div>
            </div>
            <div class="footer">
                <div class="mui-row">
                    <div class="mui-col-xs-12" v-for="(other,path,index) in others" >
                        <router-link :to="{path:other.path}">
                            <li class="liother">
                                <div class="other">
                                    <img v-bind:src="other.img">
                                </div>
                                <p>{{other.title}}</p>
                                <div class="next"><img src="../assets/images/my/next.png"></div>
                            </li>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
        <div class="bar">
        <div class="box">
            <div class="left">
                <router-link to="/">
                    <div class="icon">
                        <div class="iconImg"><img src="../assets/images/my/home.png"></div>
                        <p>首页</p>
                    </div>
                </router-link>
            </div>
            <div class="right">
                <div class="rightIcon">
                    <router-link to="/my">
                        <div class="icon">
                            <div class="iconImg"><img src="../assets/images/my_active.png"></div>
                                <p style="color:#409EFE;">我的</p>
                            </div>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
import bottomBar from '@/components/homeCom/bottomBar'
export default {
    name: 'My',
    data() {
        return {
            orders:[
                {title:"全部订单",img:require('../assets/images/my/order.png'),path:'/allorder'},
                {title:"待支付",img:require('../assets/images/my/pay.png'),path:'/waitpaydetail'},
                {title:"待出行",img:require('../assets/images/my/go.png'),path:'/waitgodetail'},
                {title:"待评价",img:require('../assets/images/my/comment.png'),path:'/waitcommentdetail'}
            ],
            others:[
                {title:"优惠券",img:require('../assets/images/my/discount.png'),path:'/'},
                {title:"发票",img:require('../assets/images/my/bill.png'),path:'/fapiao'},
                {title:"积分商城",img:require('../assets/images/my/store.png'),path:'/'},
                {title:"专属客服",img:require('../assets/images/my/kefu.png'),path:'/'}
            ],
            show:""
        }  
    },
    components:{
        'bar':bottomBar
    },
    computed: {
        isLogin(){
            if(this.$store.getters.isAuthenticated){
                return true;
            }else{
                return false;
            }
        }
    },
    created() {
            this.$axios.get("/api/sys/users/current")
            .then(data=>{
                  console.log(data.data)
                    this.$store.dispatch("setIsAuthenticated",true)
                  this.$store.dispatch("setUser",data.data)
            })
            .catch(err=>{
                console.log(err.response.data)
            })
    },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#FFFFFF')
    },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.my{
    width:100%;
}
.content{
    width:100%;
    /* float:left; */
    overflow-Y:scroll;
}
.dingdan{
    width:100%;
    background:#FFFFFF;
}

.header{
    height:220px;
    background:url("../assets/images/my/my_bg.png") no-repeat;
    background-size:100% 100%;
}

.headerOne{
    height:30px;
    line-height:30px;

    padding:30px 10px 0 10px;

}
.headerOne img{
    width:100%;
    height:100%;
}
.headerOneSet{
    display:inline-block;
    
    width:22px;
}
.headerOneMes{
    width:22px;
    display:inline-block;
    float:right;
}
.headerCon{
    width:100px;
    height:100px;
    position:absolute;
    z-index:9999;
    left:50%;
    transform:translateX(-50%);
    margin-top:30px;
    overflow:hidden;
}
.headerCon p{
    text-align:center;
    margin-top:10px;
}
.headerIcon{
    width:70px;
    height:70px;
    border-radius:50%;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.headerIcon img{
    width:100%;
    height:100%;
}
.fontLogin{
    font-size: 16px;
    color: #FFFFFF;
    font-weight: 400;
}

.mid{
    padding:15px 0;
    background:#FFFFFF;
}

.order{
    

}
.orderCon{
    width:30px;
    height:30px;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.orderCon img{
    width:100%;
    height:100%;
}

.order p{
    text-align:center;
    margin:10px 0 0 0;
}
.liother{
    width:95%;
    /* border-bottom:1px solid #E3E3E3!important; */
    /* margin-top:20px; */
    background:#FFFFFF;
    height:60px;
    position:relative;
    left:50%;
    transform:translateX(-50%);
    float:left;
}
.other{
    width:22px;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%);

}
.other img{
    width:100%;
    height:100%;
    display:block;
}
.liother p{
    position:relative;
    top:50%;
    transform:translateY(-50%);
    float:left;
    font-size:15px;
    margin-left:10px;
}
.next{
    width:25px;
    float:right;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
.next img{
    width:100%;
    height:100%;
    display:block;
    
}
.footer{
    
    margin-top:10px;
    width:100%;
    background:#FFFFFF!important;
    float:left;
    /* border:1px solid red; */
    margin-bottom:70px;
}
.bar{
    width:100%;
    /* float:left; */
    background:#FFFFFF;
     /* border:1px solid red; */
     float:left;
     position:fixed;
     bottom:0px;
     overflow-Y:hidden;
     z-index:9999;
}
.box{
    width:100%;
    /* height:40px; */
   
    float:left;
}
.left{
    width:50%;
    height:50px;
    padding-top:5px;
    float:left;
    /* border:1px solid yellow; */
}
.right{
    width:50%;
    height:50px;
    float:left;
     padding-top:5px;
    /* border:1px solid blue; */
}
.icon{
    float:left;
    position:relative;
    left:50%;
    transform:translateX(-50%);

}
.iconImg{
    width:20px;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
img{
    width:100%;
    height:100%;
    display:block;
}
p{
    text-align:center;
    font-size:12px;
}
/* .router-link-active p{

      color:#409EFE;
  } */
</style>


