<template>
    <div class="boxBig">
        <div class="select">
            <div class="box">
                <p class="fontGo">请选择出发地：</p>
                    <div class="selectGO">
                        <select>
                            <option>{{city.shortName}}</option>
                        </select>
                    </div>
                <div class="downImg"><img src="../../assets/images/discount/down.png"></div>
            </div>
            <div class="boxContent">
                <li class="libox" v-for="(routeOn) in routes.fromCitys" v-on:click="getCity(routeOn.id)" :class="{'checkOn':routeOn.id==key}">{{routeOn.name}}</li>
            </div>
            <span class="line"></span>
            <div class="carContent">
                <p>请选择车型：</p>
                <div class="selectBox">
                    <li class="libox" v-for="routeOn in routes.carInfos" v-on:click="getCar(routeOn.id)" :class="{'checkOn':routeOn.id==key2}">{{routeOn.name}}</li>
                    
                </div>
            </div>
        </div>
        <div class="payBar">
            <div class="payleft">
                <div class="callme">
                    <div class="callImg">
                        <img src="../../assets/images/callme.png">
                    </div>
                    <p>联系我</p>
                </div>
                <div class="payPrice">
                    <p>￥{{priceValue}}</p>
                    <p style="font-size:12px;margin-top:2px">.00</p>
                </div>
            </div>
            <div class="payright">
                <p class="justpay" v-on:click="buy()">立即购买</p>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    props:["route"],
    data(){
        return{
            id:this.$route.params.id,
            routes:"",
            city:"",
            key:"",
            key2:"",
            priceValue:""
            

        }
    },
    methods: {
        getCity(key){
            console.log(key)
            this.key=key
            let priceId=this.key.toString()+this.key2.toString()
            this.priceValue=this.routes.details[priceId]
            // console.log(this.routes.details)
            
        },
        getCar(key2){
            console.log(key2)
            this.key2=key2
            let priceId=this.key.toString()+this.key2.toString()
            this.priceValue=this.routes.details[priceId]
            // return priceValue
        },
        buy(){
            if(!this.priceValue){
                alert('你还没有选择出发地')
                
            }else{
                this.$router.push('/active')
            }
        }
        //  price(){
        //     let priceId=this.key.toString()+this.key2.toString()
        //     let priceValue=this.routes.details[priceId]
        //     return priceValue
        // }
    },

    
    created() {
        console.log(this.id)
        if(!window.localStorage){
            console.log("获取本地信息失败")
        }else{
            let storage=window.localStorage;
            // console.log(storage)
            let city=storage.getItem("city")
            let cityValue=JSON.parse(city)
            this.city=cityValue
            
            // console.log(this.city)
            //  console.log(this.city.id)
        }
        this.$axios.get("/api/api-whale/route/app/getRouteAndRouteDetailByRouteId",{
            params:{
                id:this.id,
                cityId:this.city.id
                
            }
        })
        .then((data)=>{
            // console.log(this.city.id)
            // console.log(data.data.data)
            // console.log(this.uid)
            this.routes=data.data.data
            // console.log(this.routes.details[4401069])
            // this.priceId=JSON.stringify(data.data.data.details)
            // console.log(this.priceId)
            // let qq=JSON.parse(this.priceId)
            // console.log(qq[4401069])
        })
    },
    
}
</script>
<style scoped>

.select{
    
    width:100%;
    background:#FFFFFF;
     /* border:1px solid red; */
     height:260px;
     /* float:left; */
     margin-top:10px;
     float:left;
}
.box{
    padding:0 10px;
    width:100%;
    float:left;
   
    

}
select{
    width:2rem;
    margin-top:-0px;
    margin-left:-10px;
    color:#2695FE;
    /* font-size: */
    /* border:1px solid blue; */
    float:left;
}
.fontGo{
    padding-top:4px;
    height:30px;
    line-height:30px;
    font-size:16px;
    float:left;
    color:#333333;
    /* border:1px solid red; */
}
.downImg{
    width:25px;
    margin-top:12px;
    margin-left:-15px;
    position:relative;
    /* top:50%;
    transform:translateY(-50%); */
    float:left;
}
img{
    width:100%;
    height:100%;
    display:block;
}
.boxContent{
    width:100%;
    float:left;
    /* border:1px solid yellow; */
    margin-top:-10px;
}
.libox{
    height: 27px;
    line-height: 27px;
    border-radius: 2px;
    border: 1px solid #666666;
    float: left;
    padding: 0 7px;
    color:#666666;
    font-size:14px;
    margin-left:15px;
    margin-bottom:15px;
    /* color:red; */
}
.liboxOn{
    background:blue;
}
.line{
    width:95%;
    height:1px;
    background:#E3E3E3;
    position:relative;
    left:50%;
    transform:translateX(-50%);
    float:left;
}
.carContent{
    width:100%;
    float:left;
    /* border:1px solid yellow; */
}
.carContent>p{
    height:30px;
    line-height:30px;
    font-size:16px;
    float:left;
    padding:0 10px;
    padding-top:5px;
    color:#333333;
}
.selectBox{
    width:100%;
    /* border:1px solid red; */
    float:left;
}
.payBar{
    width:100%;
    height:50px;
    position:fixed;
    bottom:0px;
    z-index:9999;
    background:#FFFFFF;
    float:left;
    border-top:1px solid #E3E3E3;
}
.payleft{
    width:65%;
    float:left;
}
.callme{
    margin-left:10px;
    padding-top:3px;
    float:left;
    height:50px;
    /* border:1px solid red; */
}
.callme p{
    float:left;
    font-size:12px;
    text-align:center;
    color:#3F9DFD;
}
.callImg{
    width:25px;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.payright{
    width:35%;
    height:50px;
    line-height:50px;
    text-align:center;
    float:right;
    background:#FF2A00;
}
.justpay{
    color:#FFFFFF;
    font-size:16px;
}
.payPrice{
    float:right;
    height:50px;
    line-height:50px;
    margin-right:20px;
}
.payPrice p{
    color:#FF2900;
    font-size:18px;
    float:left;
}
.checkOn{
    border:1px solid #4D9DFE;
    color:#4D9DFE;
}
</style>


