<template>
    <div class="change">
        <div class="back" @click="$router.back(-1)">
            <div class="backImg">
            <img src="../assets/images/justgo/back.png">
            </div>
            <p>更改头像</p>
            <div class="Img">
                <img src="../assets/images/icon.png">
            </div>
        </div>
        <div class="content">
            <li class="list" v-for="(list,index) in 50" v-on:click="checkOn(index)">
                <div class="iconImg">
                    <span :class="{'spanOn':index==num}"></span>
                </div>
                
            </li>
        </div>
        <div class="checkOn">
            <button class="btn">完成({{this.num}})</button>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            num:null
        }
    },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#FFFFFF')
    },
    methods: {
        checkOn(index){
            this.num=index
        }
    },
}
</script>
<style scoped>
.change{
    width:100%;
    float:left;
    background:#FFFFFF;
}
.back{
  width:100%;
  height:50px;
  line-height:50px;
  /* border:1px solid yellow; */
  background:#FFFFFF;
  padding:0 10px;
  position:fixed;
  top:0px;
}
.back p{
    margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.Img{
  width:20px;
  float:right;
  position:relative;
  top:50%;
  transform:translateY(-50%);
}
.content{
    margin-top:50px;
    padding:0 5px;
    margin-bottom:60px;
    float:left;


}
.list{
    width:25%;
    float:left;
    padding:5px;
}
.iconImg{
    width:100%;
    height:80px;
    background:#D4D4D4;
}
.checkOn{
    width:100%;
    height:60px;
    position:fixed;
    bottom:0px;
    /* border:1px solid red; */
    background-color:#F7F7F7;
}
.btn{
    float:right;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    margin-right:10px;
    background:#2695FE;
    color:#FFFFFF;
}
.list span{
    display:block;
    width:10px;
    height:10px;
    border-radius:50%;
    background:#828282;
    float:right;
    margin:5px 5px 0 0;
}
.list .spanOn{
    background:#2695FE;
}
</style>


