<template>
    <div class="goodPrice">
        <div class="box">
            <div class="titleBar">
                <span class="span"></span>
                <p>特价专线</p>
                <router-link to="/discountroute"><a>查看更多<img src="../../assets/images/gt.png"></a></router-link>
            </div>
            <div class="content">
                <div class="mui-row">
                    <div class="mui-col-xs-12">
                        <li v-for="data in discountRoutes">
                          <router-link :to="'/detail/'+data.id">
                                <div class="liImg"><img :src="imgWeb+data.coverImg"></div>
                                <div class="fontContent">
                                    <p class="title">{{data.title}}</p>
                                    <p class="cont" v-html="msg(data.routeIntro).slice(0,29)"></p>
                                    <div class="sale">
                                            <p class="fontPrice">￥{{data.price}}<span class="fontQi">起</span></p>
                                            <p class="fontSale"><s>￥{{data.originPrice}}</s></p>
                                    </div>
                                </div>
                            </router-link>
                        </li>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            discountRoutes:"",
            imgWeb:"http://m.whalebus.cn:8006"
        }
    },
    methods: {
        get(data){
            console.log(data)
        }
        
    },
    filters: {
            htmlfilter:function(val) {
            return val.replace(/<[^>]*>/g);
            }
        },
    created() {
        this.$axios.get("/api/api-whale/route/app/specialPriceLine",{
            params:{
                n:4
            },
        })
        .then((data=>{
            console.log(data.data.data)
            this.discountRoutes=data.data.data;

        }))
    },
}
</script>
<style scoped>
.goodPrice{
    margin-top:10px;
    background:#FFFFFF;
    width:100%;
    float:left;
    /* border:1px solid blue; */
}
.box{
    width:95%;
    /* border:1px solid red; */
    position:relative;
    left:50%;
    transform:translateX(-50%);
    float:left;
}
.titleBar{
    width:100%;
    height:40px;
    /* border:1px solid yellow; */
    /* position:relative; */
    float:left;
}
.titleBar .span{
    display:block;
    width:3px;
    height:15px;
    border-radius:20%;
    background:#2695FE;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%)
    
}
.titleBar p{
    float:left;
    font-size:16px;
    height:40px;
    line-height:40px;
    margin-left:10px;
    color:#2F99FC;
}
.titleBar a{
    float:right;
    color:#BFBFBF;
    font-weight:300;
    font-size:14px;
    height:40px;
    line-height:40px;
}
.titleBar a img{
    float:right;
    width:30px;
    position:relative;
    top:50%;
    transform:translateY(-50%)
}
.content{
    width:100%;
    /* border:1px solid green; */
    float:left;
}

.content li{
    width:100%;
    height:140px;
    border-bottom:1px solid #E9E9E9;
    /* float:left; */
    /* border-radius:12px; */
    overflow:hidden;
    /* box-shadow: 0px 0px 2px #888888 */
    
}
.content li:last-child{
    border-bottom:none;
}
.liImg{
    width:180px;
    height:140px;
    padding:10px;
    overflow:hidden;
    float:left;
    border-radius:40px;
    /* border:1px solid black; */
}
.liImg img{
    width:100%;
    height:100%;
}
.fontContent{
    /* padding:10px 0; */
    /* border:1px solid green; */
}
.title{
    width:100%;
    font-size:14px;
    font-weight:500;
    height:30px;
    line-height:30px;
    
    color:#333333;
    /* border:1px solid blue; */

    padding:7px  10px 0;
}
.cont{
    width:100%;
    margin-top:10px;
    font-size:12px;
    
    font-weight:100;
}
.fontPrice{
    color:#FF2900;
    font-size:18px;
    float:left;
    font-weight:600;
   
}
.fontQi{
    display:inline-block;
    color:#FE4C2A;
    font-size:12px;
    font-weight:200;
}
.sale{
    height:40px;
    float:left;
    line-height:40px;
    margin-top:-10px;
}
.fontSale{
    float:right;
    font-size:14px;
    margin-left:20px;
    font-weight:300;
}
</style>


