                <img src="../assets/images/search.png">
            </div>
        </div>
        <div class="content">
            <div class="tab">
                <li class="li" v-for="(type,index) in orderType" v-on:click="checkOn(index)">
                {{type}}
                
                    <span class="underline" v-show="index==num"></span>
                    <p class="maopao">10</p>
                
                </li>
            </div>
            <div class="tabcont">
                <div class="all" v-show="num==0">
                    <li class="paylist">
                        <div class="top">
