                        <div class="top">
                            <div class="left">
                                <div class="Img">
                                </div>
                            </div>
                            <div class="right">
                                <li class="title">
                                    <p class="titleFont">定制包车</p>
                                    <span>|</span>
                                    <p class="type">往返</p>
                                    <p class="statu">待支付</p>
                                </li>
                                <li class="begin">
                                    <p>出发地：</p>
                                    <span>东风广场</span>
                                </li>
                                <li class="begin">
                                    <p>目的地：</p>
                                    <span>白云机场</span>
                                </li>
                                <li class="begin">
                                    <p>出发时间：</p>
                                    <span>2019-6-17&nbsp;&nbsp;&nbsp;&nbsp;10：00</span>
                                </li>
                                <li class="total">
                                    <p>共1辆</p>
                                    <p class="payFont">实付款：<span>￥900.</span><span style="font-size:14px;color:red;">00</span></p>
                                </li>
