<script>
export default {
    data(){
        return{
            orderType:["全部","待支付","待出行","待评价"],
            num:0
        }
    },
    methods: {
        checkOn(index){
            this.num=index
        }
    },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#F7F7F7')
    }
}
</script>
<style scoped>
.allorder{
    width:100%;
    float:left;
    /* border:1px solid red; */
}
.back{
    width:100%;
    height:50px;
    line-height:50px;
    float:left;
    /* border:1px solid yellow; */
    background:#FFFFFF;
    padding:0 10px;
    position:fixed;
    top:0px;
    z-index:9999;
}
.back p{
    margin-left:5px;
    float:left;
    color:#333333;
}
img{
    width:100%;
    height:100%;
    display:block;
}
.backImg{
    width:20px;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    float:left;
}
.content{
    margin-top:50px;

