    width:12px;
    height:12px;
    line-height:12px;
    border-radius:50%;
    background:red;
    color:#FFFFFF;
    
    
}
.underline{
    display:block;
    width:35px;
    height:4px;
    border-radius:10px;
    background:#2695FE;
    position:relative;
    left:50%;
    margin-top:-5px;
    transform:translateX(-50%);
    
}
.checkOn{
    font-size:16px;
    color:#2695FE;
}
.tabcont{
    width:100%;
    float:left;
    margin-top:40px;
    /* border:1px solid blue; */
}
.all{
    width:100%;
    /* height:60px; */
    float:left;
    /* border:1px solid red; */
    /* background-color:#FFFFFF; */
}
.paylist{
    width:100%;
    /* padding-top:20px; */
    /* height:60px; */
    float:left;
    background-color:#FFFFFF;
    margin-bottom:10px;
}
.golist{
    width:100%;
    /* padding-top:20px; */
    /* height:60px; */
    float:left;
    background-color:#FFFFFF;
    margin-bottom:10px;
    
}
.conlist{
    width:100%;
    /* padding-top:20px; */
    /* height:60px; */
    float:left;
    background-color:#FFFFFF;
    margin-bottom:10px;
}
.left{
    /* padding-top:20px; */
    width:35%;
    height:120px;
    padding:20px 10px 0 10px;
    /* border:1px solid blue; */
    float:left;
    overflow:hidden;
}
.Img{
    width:100%;
    height:100px;
    border-radius:6px;
    background:skyblue;
    /* margin-top:-10px; */
}
.right{
    padding-top:20px;
    width:65%;
    float:left;
    padding-right:10px;
}
.title{
    width:100%;
    float:left;
    height:30px;
    line-height:30px;
}
.titleFont{
    font-size:16px;
    color:#333333;
    float:left;
}
.title span{
    display:block;
    float:left;
    color:gray;
    margin:0 10px;
}
.type{
    float:left;
}
.statu{
    float:right;
    color:red;
}
.begin{
    float:left;
    width:100%;
    height:25px;
    line-height:30px;
}
.begin p{
    float:left;
    font-size:14px;
    color:#333333;
}
.begin span{
    display:block;
    float:left;
    font-size:14px;
    font-weight:500;
}
.total{
    width:100%;
    float:left;
    
    /* border:1px solid blue; */
}
.total>p{
    height:30px;
    line-height:40px;
    float:left;
    /* border:1px solid blue; */
    margin-left:30px;
    color:#333333;
}
.payFont{
    float:right!important;
}
.payFont>span{
    color:#FF2904;
    font-size:18px;
}
.bottom{
    width:95%;
    height:60px;
    border-top:1px solid #E3E3E3;
    float:left;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.again{
    width:80px;
    float:right;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    border:1px solid red;
    color:red;
    border-radius:1px;
    margin-right:10px;
}
.btn{
    width:80px;
    float:right;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    border:1px solid #333333;
    color:#333333;
    border-radius:1px;
}

</style>


