<template>
    <div class="route">
        <div class="box">
            <div class="routein">
                <p>路线包含：</p>
                <div class="mes" v-for="routes in routes">
                    <div class="iconImg"><img src="../../assets/images/discount/check.png"></div>
                    <p>{{routes}}</p>
                </div>
                
            </div>
            <div class="routein">
                <p>路线不包含：</p>
                <div class="mes" v-for="routes in routesN">
                    <div class="iconImg"><img src="../../assets/images/discount/nocheck.png"></div>
                    <p>{{routes}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{

        }
    },
    props:["route"],

    computed: {
        routes(){
            return (this.route.feeInclude || "").split(',')
        },
        routesN(){
            return  (this.route.feeNotInclude || "").split(',')
        }
    },
}
</script>
<style scoped>
.route{
    margin-top:10px;
    width:100%;
    height:70px;
    background:#FFFFFF;
    padding-top:8px;
    float:left;
    /* border:1px solid blue; */
}
.box{
    padding:0 10px;
    width:100%;
    /* border:1px solid red; */
    float:left;
}
.routein{
    width:100%;
    height:30px;
    /* border:1px solid yellow; */
    float:left;
}
.routein>p{
    float:left;
    height:30px;
    line-height:30px;
    font-size:14px;
    color:#333333;
}
.mes{
    /* width:50%; */
    height:30px;
    line-height:30px;
    float:left;
    margin-left:5px;
    /* border:1px solid red; */
}
.iconImg{
    width:12px;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    float:left;
}
img{
    width:100%;
    height:100%;
    display:block;
}
.mes p{
    margin-left:5px;
    float:left;
    font-size:12px;
}
</style>


