<template>
    <div class="setting">
        <div class="back" @click="$router.back(-1)">
            <div class="backImg">
            <img src="../assets/images/justgo/back.png">
            </div>
            <p>设置</p>
        </div>
        <div class="content">
            <li class="list">
                <p>给我们评分</p>
                <div class="Img"><img src="../assets/images/my/next.png"></div>
            </li>
            <li class="list">
                <p>帮助与反馈</p>
                <div class="Img"><img src="../assets/images/my/next.png"></div>
            </li>
            <li class="list">
                <p>隐私政策</p>
                <div class="Img"><img src="../assets/images/my/next.png"></div>
            </li>
            <li class="list">
                <p>关于我们</p>
                <div class="Img"><img src="../assets/images/my/next.png"></div>
            </li>
        </div>
        <button class="btn" v-on:click.prevent="logout()">退出登录</button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            
        }
    },
    methods: {
        logout(){
            //删除ls
            localStorage.removeItem("jwtToken");
            //删除请求头
            this.$store.dispatch("setIsAuthenticated",false)
            this.$store.dispatch("setUser",{})
            this.$router.push('/login')
        }
    },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#FFFFFF')
    },
    
}
</script>
<style scoped>
.setting{
    width:100%;
    overflow-X:scroll;
    float:left;
}
.back{
  width:100%;
  height:50px;
  line-height:50px;
  /* border:1px solid yellow; */
  background:#FFFFFF;
  padding:0 10px;
  position:fixed;
  top:0px;
  z-index:9999;
}
.back p{
    margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.content{
    width:100%;
    margin-top:50px;
    float:left;

}
.list{
    width:100%;
    height:60px;
    line-height:60px;
    border-bottom:1px solid #E3E3E3;
    float:left;
}
.list p{
    float:left;
    padding-left:10px;
    color:#333333;
}
.Img{
    width:30px;
    float:right;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
.btn{
    position:relative;
    left:50%;
    transform:translateX(-50%);
    margin-top:100px;
    float:left;
    width:240px;
    height:40px;
    border-radius:30px;
    color:#2895FC;
    background:#EAF4FE;
    border:none;
}
</style>


