<template>
    <div class="discountRoute">
        <div class="back" @click="$router.back(-1)">
            <div class="backImg">
                <img src="../assets/images/justgo/back.png">
            </div>
            <p>特价专线</p>
        </div>
        <div class="box">
            <div class="content">
                <div class="mui-row">
                    <div class="mui-col-xs-12">
                        <li v-for="data in discountRoutes">
                            <router-link :to="'/detail/'+data.id">
                                <div class="liImg"><img :src="imgWeb+data.coverImg"></div>
                                    <div class="fontContent">
                                        <p class="title">{{data.title}}</p>
                                        <p class="cont" v-html="msg(data.routeIntro).slice(0,29)"></p>
                                        <div class="sale">
                                                <p class="fontPrice">￥{{data.price}}<span class="fontQi">起</span></p>
                                                <p class="fontSale"><s>￥{{data.originPrice}}</s></p>
                                        </div>
                                    </div>
                            </router-link>
                        </li>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
     data() {
        return {
            discountRoutes:"",
            imgWeb:"http://m.whalebus.cn:8006",
            city:""
        }
    },
    created() {
        this.$axios.get("/api/api-whale/route/app/specialPriceLine",{
            params:{
                address:this.city
            }
        })
        .then((data=>{
            console.log(data.data.data)
            this.discountRoutes=data.data.data;
            let storage=window.localStorage;
            this.city=storage.getItem("city")

        }))
    },
}
</script>
<style scoped>
.discountRoute{
    width:100%;
    float:left;
    /* border:1px solid red; */
    background:#FFFFFF;
}
.box{
    width:100%;
    /* border:1px solid red; */
    position:relative;
    left:50%;
    margin-top:50px;
    transform:translateX(-50%);
    float:left;
    background:#F7F7F7;
    overflow-Y:scroll;
}
.back{
  width:100%;
  height:50px;
  line-height:50px;
  position:fixed;
  top:0px;
  /* border:1px solid yellow; */
  background:#FFFFFF;
  padding:0 10px;
  overflow-Y:hidden;
  z-index:999999;
}
.back p{
    margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.content{
    width:100%;
    /* border:1px solid green; */
    float:left;
}

.content li{
    width:100%;
    height:140px;
    /* border-bottom:1px solid #E9E9E9; */
    margin-bottom:10px;
    /* float:left; */
    /* border-radius:12px; */
    overflow:hidden;
    background:#FFFFFF;
    /* box-shadow: 0px 0px 2px #888888 */
    
}
.content li:last-child{
    border-bottom:none;
}
.liImg{
    width:180px;
    height:140px;
    padding:10px;
    overflow:hidden;
    float:left;
    border-radius:40px;
    /* border:1px solid black; */
}
.liImg img{
    width:100%;
    height:100%;
}
.fontContent{
    padding:0;
    /* border:1px solid green; */
}
.title{
    width:100%;
    font-size:16px;
    font-weight:500;
    height:30px;
    line-height:30px;
    
    color:#333333;
    /* border:1px solid blue; */

    padding:7px  10px 0;
}
.cont{
    width:100%;
    margin-top:10px;
    font-size:14px;
    
    font-weight:100;
}
.fontPrice{
    color:#FF2900;
    font-size:18px;
    float:left;
    font-weight:600;
   
}
.fontQi{
    display:inline-block;
    color:#FE4C2A;
    font-size:12px;
    font-weight:200;
}
.sale{
    height:40px;
    float:left;
    line-height:40px;
    margin-top:-10px;
}
.fontSale{
    float:right;
    font-size:14px;
    margin-left:20px;
    font-weight:300;
}
</style>


