<template>
    <div class="carShow">
        <div class="box">
            <div class="titleBar">
                <span class="span"></span>
                <p>车型展示</p>
                <router-link to="/selectcar"><a>查看更多<img src="../../assets/images/gt.png"></a></router-link>
            </div>
            <div class="content">
                <div class="mui-row">
                    <div class="mui-col-xs-6 pad" v-for="data in carshows">
                        <router-link :to="'/carmes/'+data.id">
                        <div class="liBox">
                            <div class="liImg"><img :src="imgWeb+data.carImg"></div>
                            <div class="fontContent">
                                <p class="fontTitle">{{data.name}}</p>
                                <div class="sale">
                                <p class="fontPrice">座位<span class="fontQi">{{data.carSeat}}</span></p>
                                </div>
                            </div>

                        </div>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            carshows:"",
            imgWeb:"http://m.whalebus.cn:8006"
        }
    },
    created() {
        this.$axios.get("/api/api/api-whale/carinfo/app/page",{
            params: {
                pageNo:1,
                pageSize:4
                }
            })
        .then((data=>{
            console.log(data.data.data.records)
            this.carshows=data.data.data.records

        }))
    },
}
</script>
<style scoped>
.carShow{
    width:100%;
    float:left;
    background:#FFFFFF;
    margin-top:10px;
    /* border:1px solid yellow; */
}
.box{
    width:95%;
    /* border:1px solid red; */
    position:relative;
    left:50%;
    /* margin:15px 0; */
    transform:translateX(-50%);
    /* float:left; */
}
.titleBar{
    width:100%;
    height:40px;
    /* border:1px solid yellow; */
    /* position:relative; */
    float:left;
}
.titleBar .span{
    display:block;
    width:3px;
    height:15px;
    border-radius:20%;
    background:#2695FE;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%)
    
}
.titleBar p{
    float:left;
    font-size:16px;
    height:40px;
    line-height:40px;
    margin-left:10px;
    color:#2F99FC;
}
.titleBar a{
    float:right;
    color:#BFBFBF;
    font-weight:300;
    font-size:14px;
    height:40px;
    line-height:40px;
}
.titleBar a img{
    float:right;
    width:30px;
    position:relative;
    top:50%;
    transform:translateY(-50%)
}
.content{
    width:100%;
    /* border:1px solid green; */
    float:left;
}
.pad{
    padding: 7px;
}
.liBox{
    width:100%;
    /* padding:10px; */
    height:180px;
    /* border:1px solid blue; */
    float:left;
    border-radius:5px;
    overflow:hidden;
    box-shadow: 2px 2px 2px #F7F7F7;
    
}
.liImg{
    width:100%;
    height:120px;
    overflow:hidden;
}
.liImg img{
    width:100%;
    position:relative;
    top:-15px;
}
.fontContent{
    width:100%;
    float:left;
}
.fontTitle{
    width:100%;
    font-size:14px;
    font-weight:500;
    height:30px;
    line-height:30px;
    color:#333333;
    float:left;
    padding:4px  10px 0;
}
.sale{
    width:100%;
    height:40px;
    margin-top:-20px;
    float:left;
    line-height:40px;
    padding:0 10px;
}
.fontPrice{
    color:#FF2901;
    font-size:14px;
    float:left;
    font-weight:300;
   
}
.fontQi{
    display:inline-block;
    color:#FF2B02;
    font-size:20px;
    font-weight:200;
    margin-left:14px;
}
</style>


