<template>
    <div class="login">
        <router-link to="/my">
            <span class="comeback">
                <img src="../assets/images/back.png">
            </span>
        </router-link>
        <div class="header">
            <div class="homeImg">
                <img src="../assets/images/login/banner.png">
            </div>
        </div>
        <div class="content">
            <div class="li">
                
                <span class="icon">
                    <img src="../assets/images/login/phone.png">
                </span>
                <input type="text" placeholder="手机号码" v-model="phone">
                <span v-if="!phone==''" @click="close()" class="close">
                    <img src="../assets/images/login/close.png">
                </span>
            </div>
            <div class="li">
                <span class="icon">
                    <img src="../assets/images/login/pass.png">
                </span>
                <input type="text" placeholder="验证码" v-model="code">
                <span  v-show="show" @click="getCode()" class="getCode">获取验证码</span>
                <span v-show="!show" class="count">({{count}}s)</span>
            </div>
        </div>
        <button class="btn" v-on:click="login()">登录</button>
    </div>
</template>

<script>
import { Toast } from 'mint-ui';
import axios from 'axios'
import jwt_decode from 'jwt-decode'
export default {
    name: 'Login',
    data() {
        return {
            show: true,
            count: '',
            timer: null,
            phone:"",
            key:"",
            code:"",
            errors:""
        }
    },
    created() {
        
    },
    methods: {
        login(){
            console.log(this.$store.getters.isAuthenticated)
            this.$axios.post("/sys/login-sms?phone="+this.phone+"&code="+this.code+"&key="+this.key)
            .then(res=>{
                console.log(res.data)
                //存储token到ls
                const token=res.data.access_token
                const retoken=res.data.refresh_token
                const tokenTime=res.data.expires_in
                window.localStorage.setItem("jwtToken",token);
                window.localStorage.setItem("tokenTime",tokenTime);
                //进行action更改store的state

                this.$router.push('/my')
            })
            .catch(err=>{
                console.log(err)
                if(err.response.data){
                    
                    this.errors=err.response.data;
                    console.log(this.errors)
                    Toast(this.errors.error_description);
                }
            })
        },
        getCode(){
            this.$axios.post("/api/sms/notification-anon/codes?phone="+this.phone)
            .then((data)=>{
            // console.log(data)
            this.key=data.data.key
            // console.log(this.key)
           
        })
        const TIME_COUNT = 60;
        if (!this.timer) {
            this.count = TIME_COUNT;
            this.show = false;
            this.timer = setInterval(() => {
                if (this.count > 0 && this.count <= TIME_COUNT) {
                this.count--;
                } else {
                        this.show = true;
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                }, 1000)
            }
        },
        close(){
            if(this.phone){
                this.phone="";
            }
        }  
    },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#FFFFFF')
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
    width:100%;
    float:left;
}
.header{
    width:100%;
    float:left;
    height:240px;
    margin-top:-20px;
    /* border:1px solid red; */
}
.homeImg{
    width:120px;
    position:relative;
    left:50%;
    transform:translateX(-50%);
    bottom:-100px;
}
img{
    width:100%;
    height:100%;
    display:block;
}
.content{
    width:100%;
    float:left;
    /* border:1px solid yellow; */
}
.li{
    width:85%;
    height:60px;
    float:left;
    border-bottom:1px solid #E3E3E3;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.icon{
    display:block;
    width:25px;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
input{
    width:50%;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    border:none;
    font-size:14px;
    color:#88929C;
    /* margin-left:10px; */
}
.close{
    display:block;
    float:right;
    width:25px;
    margin-top:10px;
    margin-right:10px;
}
.getCode{
    width:100px;
    height:35px;
    line-height:35px;
    text-align:center;
    border-radius:3px;
    margin-right:10px;
    color:#88929E;
    font-size:14px;
    border:1px solid #88929E;
    display:block;
    position:relative;
    float:right;
    top:50%;
    transform:translateY(-50%);
}
.count{
    display:block;
    width:40px;
    height:40px;
    line-height:40px;
    text-align:center;
    color:#88929E;
    float:right;
    /* border:1px solid red; */
    position:relative;
    top:50%;
    transform:translateY(-50%);
    margin-right:10px;
}
.btn{
    width:70%;
    height:40px;
    background-color:#CCCCCC;
    color:#FFFFFF;
    border-radius:30px;
    position:relative;
    left:50%;
    transform:translateX(-50%);
    margin-top:60px;
    
}
.comeback{
    display:block;
    width:20px;
    /* float:left; */
    position:relative;
    top:20px;
    left:20px;
}

</style>


