<template>
    <div class="swiper">
        <div class="slide">
            <Swiper>
            <Slide>
                <div></div>
            </Slide> 
            <Slide>
                <div>2</div>
            </Slide>
            <Slide>
                <div>3</div>
            </Slide>
            <Slide>
                <div>4</div>
            </Slide>
            <Slide>
                <div>5</div>
            </Slide>
            </Swiper>
        </div>
        <div class="priceMes">
            <p class="price">￥<span>{{route.price}}</span></p>
            <p class="mes">{{route.title}}</p>
        </div>
    </div>
</template>
<script>
import { Swiper, Slide } from 'vue-swiper-component';
export default {
    data(){
        return{

        }
    },
    props:["route"],
    components: {
        Swiper,
        Slide
    }
}
</script>
<style scoped>
.swiper{
    margin-top:50px;
    width:100%;
    background:#FFFFFF;
    height:320px;
    float:left;
    /* border:1px solid red; */
}
.slide div{
    width:100%;
    height:240px;
    background:blue;
    float:left;
    border:1px solid yellow;
}
.priceMes{
    width:100%;
    /* border:1px solid green; */
    padding:0 10px;
    float:left;
}
.price{
    height:50px;
    line-height:50px;
    font-size:18px;
    color:#FE2900;
    /* border:1px solid red; */
}
.mes{
    display:block;
    height:30px;
    line-height:30px;
    color:#323232;
    font-size:14px;
    /* border:1px solid blue; */
    margin-top:-20px;


}
</style>


