<template>
    <div class="discount">
        <div class="box">
            <div class="titleBar">
                <span class="span"></span>
                <p>成功案例</p>
                <a>查看更多<img src="../../assets/images/gt.png"></a>
            </div>
            <div class="content">
                <div class="mui-row">
                    <div class="mui-col-xs-12">
                        <li v-for="hotRoute in hotRoutes">
                            <div class="liImg"><img :src="imgWeb+hotRoute.coverImg"></div>
                            <div class="fontContent">
                                <p class="title">珠海拱北口岸接送专线</p>
                                <p class="cont">用车自由顺风车包车，单程包车双程来回包车，价格更优惠</p>
                                <button class="btn">查看详情</button>
                            </div>
                        </li>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            hotRoutes:"",
            imgWeb:"http://m.whalebus.cn:8006"
        }
    },
    created() {
        axios.get("/api/api-whale/route/app/hotTripLine")
        .then((data=>{
            console.log(data.data.data)
            this.hotRoutes=data.data.data;

        }))
    },
}
</script>
<style scoped>
.discount{
    margin-top:10px;
    width:100%;
    background:#FFFFFF;
    float:left;
}
.box{
    width:95%;
    /* border:1px solid red; */
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.titleBar{
    width:100%;
    height:40px;
    /* border:1px solid yellow; */
    /* position:relative; */
    float:left;
}
.titleBar .span{
    display:block;
    width:3px;
    height:15px;
    border-radius:20%;
    background:#2695FE;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%)
    
}
.titleBar p{
    float:left;
    font-size:16px;
    height:40px;
    line-height:40px;
    margin-left:10px;
    color:#2F99FC;
}
.titleBar a{
    float:right;
    color:#BFBFBF;
    font-weight:300;
    font-size:14px;
    height:40px;
    line-height:40px;
}
.titleBar a img{
    float:right;
    width:30px;
    position:relative;
    top:50%;
    transform:translateY(-50%)
}
.content{
    width:100%;
    /* border:1px solid green; */
    float:left;
}

.content li{
    width:100%;
    height:140px;
    overflow:hidden;

}
.content li:last-child{
    border-bottom:none;
}
.liImg{
    width:180px;
    height:140px;
    padding:10px;
    overflow:hidden;
    float:left;
    border-radius:40px;
}
.liImg img{
    width:100%;
    height:100%;
}
.fontContent{
    /* padding:10px 0; */
}
.title{
    width:100%;
    font-size:16px;
    font-weight:500;
    height:30px;
    line-height:30px;
    color:#333333;
    padding:7px  10px 0;
}
.cont{
    width:100%;
    margin-top:10px;
    font-size:14px;  
    font-weight:100;
}
.btn{
    color:#5FA7FE;
    height:25px;
    line-height:25px;
    padding:0 8px;
    font-size:12px;
    font-weight:100;
    border:1px solid #5FA7FE;
    border-radius:5px;
}
</style>


