<template>
    <div class="fapiao">
        <div class="back" @click="$router.back(-1)">
            <div class="backImg">
            <img src="../assets/images/justgo/back.png">
            </div>
            <p>申请发票</p>
        </div>
        <p class="invoiceType">发票类型</p>
        <div class="invoiceView">
            <button class="btn">
                电子增值税普通发票
            </button>
        </div>
        <div class="invoiceDetail">
            <li>
                <p>发票的抬头</p>
                <input type="text" placeholder="请填写公司的名称">
            </li>
             <li>
                <p>税务登记号</p>
                <input type="text" placeholder="请输入企业纳税识别号">
            </li>
             <li>
                <p>订单编号</p>
                <span>124782774921</span>
            </li>
        </div>
        <div class="invoiceOther">
            <li>
                <p>发票金额</p>
                <div class="price">
                    <p class="priceFont">900.00</p>
                    <p class="yuan">元</p>
                </div>
            </li>
            <li>
                <p>申请时间</p>
                <span>2019.06.18</span>
            </li>
        </div>
        <button class="checkOn">提交申请</button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            
        }
    },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#F7F7F7')
    },
}
</script>
<style scoped>
.fapiao{

    width:100%;
    float:left;
}
.back{
  width:100%;
  height:55px;
  line-height:55px;
  /* border:1px solid yellow; */
  background:#FFFFFF;
  padding:0 10px;
  float:left;
}
.back p{
    margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.invoiceType{
    padding:0 10px;
    width:100%;
    float:left;
    height:50px;
    line-height:50px;
    font-size:16px;
    color:#333333;
    /* border:1px solid red; */
}
.invoiceView{
    padding:0 10px;
    width:100%;
    height:80px;
    /* border:1px solid red; */
    float:left;
    background:#FFFFFF;
    margin-top:-10px;
}
.btn{
    float:left;
    
    height:50px;
    color:#FE0000;
    border:1px solid #FE0000;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
.invoiceDetail{
    background:#FFFFFF;
    
    width:100%;
    /* border:1px solid yellow; */
    float:left;
    margin-top:10px;
}
.invoiceDetail li{
    padding:0 10px;
    width:100%;
    height:60px;
    line-height:60px;
    float:left;
    border-bottom:1px solid #E3E3E3;
}
.invoiceDetail p{
    width:30%;
    float:left;
}
input{
    width:60%;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    border:none;
    font-size:14px;
}
.invoiceDetail li:last-child{
    border:none;
}
.invoiceOther{ 
    margin-top:10px;
    background:#FFFFFF;
    
    width:100%;
    float:left;
}
.invoiceOther li{
    padding:0 10px;
    width:100%;
    height:60px;
    line-height:60px;
    float:left;
    border-bottom:1px solid #E3E3E3;
}
.invoiceOther li:last-child{
    border:none;
}
.invoiceOther p{
    width:30%;
    float:left;
}
.price{
    width:60%;
    float:left;
    margin-left:15px;
}
.price p{
    font-size:18px;
    color:#FF2A02;
    float:left;
    /* border:1px solid blue; */
}
.yuan{
    float:left!important;
    color:black!important;
    font-size:14px!important;
    /* border:1px solid blue; */
}
span{
    display:block;
    float:left;
    margin-left:15px;
}
.checkOn{
    float:left;
    position:relative;
    left:50%;
    transform:translateX(-50%);
    margin-top:50px;
    width:65%;
    height:40px;
    border-radius:30px;
    background-color:#CCCCCC;
    color:#FFFFFF;
}
</style>


