<template>
    <div class="selectcar">
        <div class="back" @click="$router.back(-1)">
            <div class="backImg">
            <img src="../assets/images/justgo/back.png">
            </div>
            <p>选择车型</p>
        </div>
        <div class="content">
            <div class="tab">
                <div class="li" v-on:click="onclick(5)" :class="{'checkOn':num==5}">
                    全部
                    <span v-show="num==5"></span>
                </div>
                <div class="li" v-for="(cartype,index) in carTypes" v-on:click="onclick(index)" :class="{'checkOn':num==index}">
                    {{cartype.name}}
                    <span v-show="num==index"></span>
                </div>
            </div>
            <div class="tabcon" >
                
                <div class="licon" v-for="car in allcar" v-show="num==5" v-on:click="getId(car.id)">
                <router-link v-bind:to="'/carmes/'+car.id">
                    <div class="left">
                        <div class="carImg">
                            <img :src="imgWeb+car.carImg">
                        </div>
                    </div>
                    <div class="right">
                        <p class="title"></p>
                        <p class="mesNum">座位数：{{car.carSeat}}<span>人</span></p>
                        <p class="mes">含8小时100公里</p>
                        <p class="price">￥650<span>/&nbsp;&nbsp;天</span></p>
                    </div>
                </router-link>
                </div>
                
                <div class="licon" v-for="car in carlist" v-on:click="getId(car.id)">
                    <router-link v-bind:to="'/carmes/'+car.id">
                        <div class="left">
                            <div class="carImg">
                                <img :src="imgWeb+car.carImg">
                            </div>
                        </div>
                        <div class="right">
                            <p class="title"></p>
                            <p class="mesNum">座位数：{{car.carSeat}}<span>人</span></p>
                            <p class="mes">含8小时100公里</p>
                            <p class="price">￥650<span>/&nbsp;&nbsp;天</span></p>
                        </div>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            carTypes:"",
            num:5,
            imgWeb:"http://m.whalebus.cn:8006",
            carlist:"",
            allcar:""
            
        }
    },
    methods: {
        onclick(index){
            this.num=index;
            this.$axios.get('/api/api-whale/carinfo/app/all',{
                params:{
                    carType:index+1
                }
            })
            .then((data=>{
                console.log(data.data.data)
                this.carlist=data.data.data
            }))
        },
        getId(id){
            console.log(id)
        }
    },
    created() {
        this.$axios.get('/api/api-whale/carinfo/app/allCarType')
        .then((data=>{
            console.log(data.data.data)
            this.carTypes=data.data.data
            // this.carTypes.push({id:6,name:"全部"})
            console.log(this.carTypes)

        }))
        this.$axios.get('/api/api-whale/carinfo/app/all')
        .then((data)=>{
            // console.log(data.data.data)
            this.allcar=data.data.data
        })
    }
}
</script>
<style scoped>
.selectcar{
    width:100%;
}
.back{
  width:100%;
  height:50px;
  line-height:50px;
  /* border:1px solid yellow; */
  background:#FFFFFF;
  padding:0 10px;
  float:left;
      position:fixed;
    top:0px;
    z-index:999999;

}
.back p{
    margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.content{
    margin-top:50px;
    width:100%;
    float:left;
    background:#FFFFFF;
}
.tab{
    width:100%;
    float:left;
    position:fixed;
    top:50px;
    background:#FFFFFF;
    z-index:999;
}
.li{
    width:16.6%;
    height:30px;
    line-height:30px;
    font-size:14px;
    /* border:1px solid red; */
    float:left;
    text-align:center;
}
.checkOn{
    font-size:18px;
}
.li>span{
    width:40px;
    height:5px;
    background:#2695FE;
    border-radius:5px;
    display:block;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.tabcon{
    width:100%;
    float:left;
    margin-top:15px;
    padding:0 20px;
    overflow-Y:scroll;
    /* border:1px solid yellow; */
}
.licon{
    /* margin-top:10px; */
    width:100%;
    /* height:120px; */
    /* border:1px solid green; */
    float:left;
    margin:5px 0;
    overflow:hidden;
}
.left{
    float:left;
}
.carImg{
    width: 150px;
    float:left;
    border-radius: 8px;
    background:#FFFFFF;
    /* border:1px solid red; */
    box-shadow:1px 1px 8px #F7F7F7;
}
img{
    width:100%;
    height:100%;
}
.right{
    margin-top:20px;
    float:left;
    margin-left:10px;
}
.title{
    font-size:16px;
    color:#333333;
    font-weight:500
}
.mesNum{
    font-size:14px;
    color:#676767;
}
.mesNum>span{
    display:inline-block;
    color:#333333;
}
.mes{
    color:#999999;
}
.price{
    color:#FF2B01;
    font-size:18px;
    font-weight:600;
}
.price>span{
    display:inline-block;
    color:#FF2A02;
    font-size:16px;
    margin-left:8px;
    font-weight:500;
}
</style>


