<template>
    <div class="carmes">
        <div class="back" @click="$router.back(-1)">
            <div class="backImg">
            <img src="../assets/images/justgo/back.png">
            </div>
            <p>选择车型</p>
        </div>
        <div class="content">
          <top v-bind:car="car"></top>
          <price-detail v-bind:car="car"></price-detail>
          <carshowpic v-bind:car="car"></carshowpic>
        </div>
    </div>
</template>
<script>
import header from '@/components/carMesCom/header'
import priceDetail from '@/components/carMesCom/priceDetail'
import carshowpic from '@/components/carMesCom/carshowpic'
export default {
    data() {
      return {
          id:this.$route.params.id,
          car:""
      }
    },
    
    
    created() {
      this.$axios.get("/api/api-whale/carinfo/app/get",{
        params:{
          id:this.id
        }
      })
      .then((data)=>{
        console.log(data.data.data)
        this.car=data.data.data
        console.log(this.car)
      })
    },
    watch: {
    '$route' (to, from) { 
      if(this.$route.params.id){
          this.$axios.get("/api/api-whale/carinfo/app/get",{
          params:{
          id:this.id
        }
      })
        .then((data)=>{
          this.car=data.data.data
        })
      }
      }
    },
    mounted() {
      document.querySelector('body').setAttribute('style', 'background-color:#F7F7F7')
    },
    components:{
      "top":header,
      priceDetail,
      carshowpic
    }
}
</script>
<style scoped>
.carmes{
    width:100%;
    float:left;
    /* border:1px solid yellow; */
}
.content{
  width:100%;
  /* background:#FFFFFF; */
  float:left;
}
.back{
  width:100%;
  height:50px;
  line-height:50px;
  /* border:1px solid yellow; */
  background:#FFFFFF;
  padding:0 10px;
  float:left;
}
.back p{
  margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
</style>


