<template>
    <div class="priceDetail">
        <div class="box">
            <div class="titleBar">
                <span class="span"></span>
                <p>费用详情</p>
            </div>
            <div class="content">
                <div class="one">
                    <li class="list">
                        <p class="list_left">包车类型：</p>
                        <p class="list_right">往返接送</p>
                    </li>
                    <li class="list">
                        <p class="list_left">车型：</p>
                        <p class="list_right">往返接送</p>
                    </li>
                    <li class="list">
                        <p class="list_left">客座数：</p>
                        <p class="list_right">往返接送</p>
                    </li>
                </div>
                <div class="two">
                    <p class="list_left_two">费用包含：</p>
                    <p class="list_right_two">送往返接送往返接送往返接送往返接送往返接送往返接送</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{

        }
    }
}
</script>
<style scoped>
.priceDetail{
    margin-top:10px;
    width:100%;
    background:white;
    float:left;
}
.box{
    width:100%;

}
.titleBar{
    width:95%;
    height:40px;
    position:relative;

    left:50%;
    transform:translateX(-50%)
}
.titleBar .span{
    display:block;
    width:3px;
    height:15px;
    border-radius:20%;
    background:#333333;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%)
    
}
.titleBar p{
    float:left;
    font-size:14px;
    height:40px;
    line-height:40px;
    margin-left:10px;
    color:#333333;
}
.content{
    width:100%;
    /* border:1px solid blue; */
    margin-top:-15px;
    float:left;
}
.one{
    width:100%;
}
.list{
    width:95%;
    height:55px;
    line-height:55px;
    border-bottom:1px solid #E3E3E3;
    float:left;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.list_left{
    width:120px;
    /* border:1px solid yellow; */
    float:left;
}
.list_right{
    color:#333333;
}
.list_left_two{
    width:120px;

    float:left;
}
.list_right_two{
    height:100%;
    width:220px;
    color:#333333;
    /* border:1px solid yellow; */
    float:left;
}
.two{
    width:95%;
    /* height:50px; */
    line-height:35px;
    /* border:1px solid green; */
    float:left;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
</style>


