<template>
  <div class="justgo">
    <div class="header">
      <div class="backImg">
        <img src="../assets/images/justgo/back.png">
      </div>
      <router-link to="/"><p>返回</p></router-link>
      <div class="mapImg">
        <img src="../assets/images/justgo/map.png">
      </div>
    </div>
    <div class="content">
      <div class="tab">
        <div class="mui-row">
          <div class="mui-col-xs-4"  v-for="(tab,index) in tabs" >
            <li class="li" v-on:click="tabOn(index)">
                <p>{{tab}}</p>
                <span v-show="index==num" class="blueLine"></span>
            </li>
          </div>
        </div>
      </div>
      <div class="tabCon">
        <div v-show="num==0">
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/my.png"></div>
              <input type="text" placeholder="联系人">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/phone.png"></div>
              <input type="text" placeholder="根据登录回显">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/start.png"></div>
              <input type="text" placeholder="出发地(根据首页的回显)">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/mid.png"></div>
              <input type="text" placeholder="请输入途径点">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/end.png"></div>
              <input type="text" placeholder="目的地(根据首页的回显)">
            </li>
          </div>
          <div class="liCon" v-on:click="begin()">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/gotime.png"></div>
              <input type="text" placeholder="请选择出发的时间"  :value='beginTime' readonly="readonly" />
              
            </li>
          </div>
          <div class="liCon" v-on:click="end()">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/backtime.png"></div>
              <input type="text" placeholder="请选择回程的时间" :value='endTime'>
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/keepday.png"></div>
              <input type="text" placeholder="包天天数">
            </li>
          </div>
          
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/mannum.png"></div>
              <input type="text" placeholder="乘坐人数(根据首页的回显)">
            </li>
          </div>
        </div>
        <!-- 单程 -->
        <div v-show="num==1">
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/my.png"></div>
              <input type="text" placeholder="联系人">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/phone.png"></div>
              <input type="text" placeholder="根据登录回显">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/start.png"></div>
              <input type="text" placeholder="出发地(根据首页的回显)">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/mid.png"></div>
              <input type="text" placeholder="请输入途径点">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/end.png"></div>
              <input type="text" placeholder="目的地(根据首页的回显)">
            </li>
          </div>
          <div class="liCon" v-on:click="begin()">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/gotime.png"></div>
              <input type="text" placeholder="请选择出发的时间" :value='beginTime'>
            </li>
          </div>
          
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/mannum.png"></div>
              <input type="text" placeholder="乘坐人数(根据首页的回显)">
            </li>
          </div>
        </div>
        <!-- 包天 -->
        <div v-show="num==2">
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/my.png"></div>
              <input type="text" placeholder="联系人">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/phone.png"></div>
              <input type="text" placeholder="根据登录回显">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/start.png"></div>
              <input type="text" placeholder="出发地(根据首页的回显)">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/end.png"></div>
              <input type="text" placeholder="目的地(根据首页的回显)">
            </li>
          </div>
          <div class="liCon" v-on:click="begin()">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/gotime.png"></div>
              <input type="text" placeholder="请选择出发的时间" :value='beginTime'>
            </li>
          </div>
          <div class="liCon" v-on:click="end()">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/backtime.png"></div>
              <input type="text" placeholder="请选择回程的时间" :value='endTime'>
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/keepday.png"></div>
              <input type="text" placeholder="包天天数">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/mannum.png"></div>
              <input type="text" placeholder="乘坐人数(根据首页的回显)">
            </li>
          </div>
        </div>
      </div>
    </div>
    <router-link to="/checkorder"><button class="btn">确认</button></router-link>
    <mt-datetime-picker
      ref="begin"
      type="datetime"
      v-model="beginValue">
    </mt-datetime-picker>
    <mt-datetime-picker
      ref="end"
      type="datetime"
      v-model="endValue">
    </mt-datetime-picker>
  </div>
</template>

<script>
import { formatDate } from '@/assets/js/date.js'
export default {
  name: 'JustGO',
  data () {
    return {
      tabs: ["往返", "单程","包天"],
      num:0,
      beginValue:"",
      endValue:"",
    }
  },
  filters: {
    formatDate(time){
        var date = new Date(time);
        return formatDate(date)
      }
  },
  computed: {
    beginTime() {
      if(!this.beginValue==""){
        return formatDate(this.beginValue);
      } 
    },
    endTime() {
      if(!this.endValue==""){
        return formatDate(this.endValue);
      } 
    }
  },
  methods: {
    tabOn(index){
      this.num = index;
      
    },
    begin(){
      this.$refs.begin.open();
    },
    end(){
      this.$refs.end.open();
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.justgo{
  width:100%;
  float:left;
  background:#FFFFFF;
}
.header{
  width:100%;
  height:50px;
  line-height:50px;
  /* border:1px solid yellow; */

  overflow:auto;
  padding:0 10px;
}
.header p{
  float:left;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.mapImg{
  width:20px;
  float:right;
  position:relative;
  top:50%;
  transform:translateY(-50%);
}
.tab{
  width:100%;
  /* border:1px solid red; */
  float:left;
  margin-top:-10px;
  

  
}

.li{
  display:block;
  /* border:1px solid red; */
  float:left;
  height:40px;
  line-height:40px;
  text-align:center;
  position:relative;
  left:50%;
  transform:translateX(-50%);

}
.tab .li p{
  font-size:12px;
  font-weight:500;
  color:#333333;
}
.blueLine{
  display:block;
  width:35px;
  height:2px;
  border-radius:1px;
  background:#2695FE;
  margin-top:-15px;

}
.tabCon{
  width:100%;
  float:left;
  margin-top:10px;
  transition:all 3s linear;
}
.liCon{
  width:100%;
  float:left;
}
.liCon li{
  width:90%;
  height:45px;
  border-bottom:1px solid #E2E2E2;
  /* float:left; */
  position:relative;
  left:50%;
  transform:translateX(-50%);
}
.allImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.liCon li input{

  width:85%;
  height:35px;
  line-height:35px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
  /* margin-left:5px; */
  font-size:14px;
  font-weight:500;
  color:#999999;
  border:none;
  
}
.btn{
  background: #2695FE;
  width: 270px;
  color: #FFFFFF;
  border-radius: 20px;
  height: 40px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  border: none;
  margin-top: 20px;
}

</style>


