<template>
<div class="checkOrder">
  <div class="title">
      <div class="backImg" @click="$router.back(-1)">
        <img src="../assets/images/justgo/back.png">
      </div>
      <router-link to="/"><p>确认订单</p></router-link>
  </div>
  <div class="boxscroll">
    <div class="header">
      <div class="box">
          <li v-for="(good,index) in goods.slice(0,6)" :class="{'checkOn':index==num}">
            <div class="liImg"  v-on:click="btn(index)" :class="{'checkImg':index==num}" ></div>
            <div class="liFont" :class="{'checkFont':index==num}">{{good.id}}</div>
          </li>  
      </div>
    </div>
    <div class="content">
      <li>
          <p>车型:</p>
          <span>33客座金龙大巴</span>
      </li>
      <li>
          <div>
              <p>客座数:</p>
              <span>33人</span>
          </div>
          <div>
              <p>数量:</p>
              <span></span>
          </div>
      </li>
      <li>
          <div>
              <p>联系人:</p>
              <span>李逸</span>
          </div>
          <div>
              <p>联系电话:</p>
              <span>13172235588</span>
          </div>
      </li>
    </div>
    <priceCom></priceCom>
    <other></other>
    <exit></exit>
  </div>
  
 
  <!-- <button v-on:click="getVal(goods[num].id)" style="width:300px;height:300px;">获取</button>
  
   <div v-if="goods[num]!==undefined">{{goods[num].title}}</div> 
     
  
   <div>{{gg}}</div> -->
  </div>
</template>

<script>
import PriceDetailCom from '@/components/orderCom/PriceDetailCom'
import PriceOtherPay from '@/components/orderCom/PriceOtherPay'
import ChangeExit from '@/components/orderCom/ChangeExit'
export default {
  data() {
    return {
      num:1,
      goods:[],
      retun:"",
      gg:''
      
    }
  },
  components:{
    "priceCom":PriceDetailCom,
    "other":PriceOtherPay,
    "exit":ChangeExit
  },
  methods: {
    btn(index){
      console.log(index)
      this.num=index
    },
    getVal(retun){
      console.log(retun)
    },
    gotovalue(){
      console.log(this.gg)
    }
  },
  mounted() {
    document.querySelector('body').setAttribute('style', 'background-color:#F7F7F7')
  },
  created() {
    this.$axios.get("http://jsonplaceholder.typicode.com/posts")
    .then((data)=>{
      console.log(data.data)
      this.goods=data.data
    })
  }

}
</script>
<style scoped>
.boxscroll{
  width:100%;
  /* height:600px; */
  float:left;
  /* border:1px solid red; */
}
/* .checkOrder{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    background-color: #F7F7F7;
    
    
} */
.title{
  width:100%;
  height:50px;
  line-height:50px;
  position: fixed;
  top: 0px;
  /* border:1px solid yellow; */
  /* position:fixed; */
  background:#FFFFFF;
  padding:0 10px;
}
.title p{
  float:left;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.box{
  margin-top:50px;
  width:100%;
  height:150px;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space:nowrap;
   background:#FFFFFF;
  /* border:1px solid black; */
}
.box li{
  display:inline-block;
  width:30%;
  height:90px;
  margin-right:10px;
  border-radius:10px;

}
.checkOn{
  /* transition:all 0.2s linear; */
  width:40%!important;
  height:120px!important;
  background:blue;

}
.checkImg{
  width:100%!important;
  height:120px!important;
  
}
.checkFont{
  height:30px!important;
  line-height:30px!important;
  font-size:18px;
  color:red;
}
.liImg{

  width:100%;
  height:90%;
  background:red;
  border-radius:10px;
 
 
}
.liFont{
  width:100%;
  text-align:center;
  height:30px;
  line-height:30px;
  float:left;
}
 ::-webkit-scrollbar {
     width: 0 !important;height: 0;
   }
html {
overflow-y: scroll;
}

:root {
overflow-y: auto;
overflow-x: hidden;
}

:root body {
position: absolute;
}

body {
width: 100vw;
overflow: hidden;
}
.content{
  mar
  width:100%;
  height:150px;
  /* border:1px solid red; */
   background:#FFFFFF;
}
.content p{
  float:left;
  color:#343434;
  font-size:16px;
  line-height:40px;
  padding:0 8px;
}
.content span{
  display:block;
  font-size:14px;
  float:left;
  color:#8C8C8C;
  line-height:40px;
  padding-left:10px;
}
.content li{
  width:100%;
  height:40px;
  float:left;
}
.content div{
  height:40px;
  width:50%!important;
  float:left;
}
</style>


