<template>
    <div class="bar">
        <div class="box">
            <div class="left">
                <router-link to="/"><div class="icon">
                    <div class="iconImg"><img src="../../assets/images/home.png"></div>
                    <p style="color:#409EFE;">首页</p>
                    </div>
                </router-link> 
            </div>
            <div class="right">
                <div class="rightIcon">
                    <router-link to="/my"><div class="icon">
                        <div class="iconImg"><img src="../../assets/images/my.png"></div>
                            <p>我的</p>
                        </div>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{

        }
    }
}
</script>
<style scoped>
.bar{
    width:100%;
    /* float:left; */
    background:#FFFFFF;
     /* border:1px solid red; */
     float:left;
     position:fixed;
     bottom:0px;
     overflow-Y:hidden;
     z-index:9999;
}
.box{
    width:100%;
    /* height:40px; */
   
    float:left;
}
.left{
    width:50%;
    height:50px;
    padding-top:5px;
    float:left;
    /* border:1px solid yellow; */
}
.right{
    width:50%;
    height:50px;
    float:left;
     padding-top:5px;
    /* border:1px solid blue; */
}
.icon{
    float:left;
    position:relative;
    left:50%;
    transform:translateX(-50%);

}
.iconImg{
    width:20px;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
img{
    width:100%;
    height:100%;
    display:block;
}
p{
    text-align:center;
    font-size:12px;
}
/* .router-link-active p{

      color:#409EFE;
  } */
</style>


