<template>
    <div class="active">
        <div class="header">
            <div class="backImg">
                <img src="../assets/images/justgo/back.png">
            </div>
            <router-link to="/"><p>返回</p></router-link>
            <div class="mapImg">
                <img src="../assets/images/justgo/map.png">
            </div>
        </div>
        <div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/my.png"></div>
              <input type="text" placeholder="联系人">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/phone.png"></div>
              <input type="text" placeholder="根据登录回显">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/start.png"></div>
              <input type="text" placeholder="上车点">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/end.png"></div>
              <input type="text" placeholder="目的地（根据专线的目的地）">
            </li>
          </div>
          <div class="liCon">
            <li v-on:click="begin()">
              <div class="allImg"><img src="../assets/images/justgo/gotime.png"></div>
              <input type="text" placeholder="请选择出发的时间" :value='beginTime' readonly="readonly">
            </li>
          </div>
          <div class="liCon">
            <li v-on:click="end()">
              <div class="allImg"><img src="../assets/images/justgo/backtime.png"></div>
              <input type="text" placeholder="请选择回程的时间" :value='endTime' readonly="readonly">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/justgo/mannum.png"></div>
              <input type="text" placeholder="乘坐人数">
            </li>
          </div>
          <div class="liCon">
            <li>
              <div class="allImg"><img src="../assets/images/beizhu.png"></div>
              <input type="text" placeholder="请认真备注其他需求">
            </li>
          </div>
        <button class="btn">确认</button>
        </div>
        <mt-datetime-picker
      ref="begin"
      type="datetime"
      v-model="beginValue">
    </mt-datetime-picker>
    <mt-datetime-picker
      ref="end"
      type="datetime"
      v-model="endValue">
    </mt-datetime-picker>
    </div>
</template>
<script>
import { formatDate } from '@/assets/js/date.js'
export default {
    data(){
        return{
          beginValue:"",
          endValue:""
        }
    },
    filters: {
    formatDate(time){
        var date = new Date(time);
        return formatDate(date)
      }
  },
  computed: {
    beginTime() {
      if(!this.beginValue==""){
        return formatDate(this.beginValue);
      } 
    },
    endTime() {
      if(!this.endValue==""){
        return formatDate(this.endValue);
      } 
    }
  },
  methods: {
    tabOn(index){
      this.num = index;
      
    },
    begin(){
      this.$refs.begin.open();
    },
    end(){
      this.$refs.end.open();
    }
  },
    mounted() {
      document.querySelector('body').setAttribute('style', 'background-color:#FFFFFF')
    },

}
</script>
<style scoped>
.header{
  width:100%;
  height:50px;
  line-height:50px;
  /* border:1px solid yellow; */
  overflow:auto;
  padding:0 10px;
}
.header p{
  float:left;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.mapImg{
  width:20px;
  float:right;
  position:relative;
  top:50%;
  transform:translateY(-50%);
}
.liCon{
  width:100%;
  float:left;
}
.liCon li{
  width:90%;
  height:45px;
  border-bottom:1px solid #E2E2E2;
  /* float:left; */
  position:relative;
  left:50%;
  transform:translateX(-50%);
}
.allImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.liCon li input{

  width:85%;
  height:35px;
  line-height:35px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
  /* margin-left:5px; */
  font-size:14px;
  font-weight:500;
  color:#999999;
  border:none;
  
}
.btn{
  background: #2695FE;
  width: 270px;
  color: #FFFFFF;
  border-radius: 20px;
  height: 40px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  border: none;
  margin-top: 20px;
}
</style>


