<template>
    <div class="detail">
        <div class="back" @click="$router.back(-1)">
            <div class="backImg">
            <img src="../assets/images/justgo/back.png">
            </div>
            <p>返回</p>
        </div>
        <swiper-com v-bind:route="route"></swiper-com>
        <introduce-com v-bind:route="route"></introduce-com>
        <route-com v-bind:route="route"></route-com>
        <select-com v-bind:route="route"></select-com>
        <detail-com v-bind:route="route"></detail-com>
    </div>
</template>
<script>
import swiperCom from '@/components/detailCom/swiperCom'
import introduceCom from '@/components/detailCom/introduceCom'
import routeCom from '@/components/detailCom/routeCom'
import selectCom from '@/components/detailCom/selectCom'
import detailCom from '@/components/detailCom/detailCom'
export default {
    data(){
        return{
            id:this.$route.params.id,
            route:""
            
            
        }
    },

    created() {
        this.$axios.get("/api/api-whale/route/app/getRouteAndRouteDetailByRouteId",{
            params:{
                id:this.id
            }
        })
        .then((data=>{
            console.log(data.data.data.route)
            this.route=data.data.data.route;
        }))

        
    },
    // computed: {
    //     getlist:function(webImg){
    //         this.web=this.webImg.filter((data)=>{
    //             return data.replace(/<[^>]*>/g,"")
    //         })
    //     }
    // },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#F7F7F7')

    },
    
    components:{
        swiperCom,
        introduceCom,
        routeCom,
        selectCom,
        detailCom
    }
}
</script>
<style scoped>
.detail{
    width:100%;
    float:left;
}
.back{
  width:100%;
  height:50px;
  line-height:50px;
  /* border:1px solid yellow; */
  background:#FFFFFF;
  padding:0 10px;
  float:left;
  position:fixed;
  top:0px;
  z-index:9999;
}
.back p{
    margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}  

</style>


