<template>
    <div class="partner">
        <div class="box">
            <div class="titleBar">
                <span class="span"></span>
                <p>合作伙伴</p>
                <a>查看更多<img src="../../assets/images/gt.png"></a>
            </div>
            <div class="content">
                <div class="mui-row">
                    <div class="mui-col-xs-4  pad" v-for="data in hotRoutes"> 
                        <li>
                            <div class="liImg"><img :src="imgWeb+data.img"></div>
                            <!-- <div>{{data.name}}</div> -->
                        </li>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            hotRoutes:"",
            imgWeb:"http://m.whalebus.cn:8006"
        }
    },
    created() {
        this.$axios.get("/api/api-whale/ad/app/page",{
            params:{
                pageNo:1,
                pageSize:6
            }
        })
        .then((data=>{
            console.log(data.data.data.records)
            this.hotRoutes=data.data.data.records;

        }))
    },
}
</script>
<style scoped>
.partner{
    background:#FFFFFF;
    width:100%;
    float:left;
    margin-top:10px;
    margin-bottom:60px;
    padding-bottom:10px;
}
.box{
    width:95%;
    /* border:1px solid red; */
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.titleBar{
    width:100%;
    height:40px;
    /* border:1px solid yellow; */
    /* position:relative; */
    float:left;
}
.titleBar .span{
    display:block;
    width:3px;
    height:15px;
    border-radius:20%;
    background:#2695FE;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%)
    
}
.titleBar p{
    float:left;
    font-size:16px;
    height:40px;
    line-height:40px;
    margin-left:10px;
    color:#2F99FC;
}
.titleBar a{
    float:right;
    color:#BFBFBF;
    font-weight:300;
    font-size:14px;
    height:40px;
    line-height:40px;
}
.titleBar a img{
    float:right;
    width:30px;
    position:relative;
    top:50%;
    transform:translateY(-50%)
}
.content{
    width:100%;
    float:left;
}

.content li{
    width:100%;
    height:90px;
    border-radius:6px;
    overflow:hidden;
    box-shadow: 1px 1px 2px #888888;
}
.pad{
    padding: 5px;
}
.liImg{
    /* width:100%; */
    height:90px;
    /* border:1px solid black; */
}
.liImg img{
    width:100%;
    height:100%;
}
</style>


