        <div class="box">
            <li>
                <div class="title">
                    <span></span>
                    <p>[往返价格]</p>
                </div>
                <div class="content">
                    <p>包含：车辆使用费、燃油费、司机服务费、乘客责任险；</p>
                    <p>不含：路桥费、停车费、司机工作餐及出发地市以外的住宿费。</p>
                </div>
            </li>
            <li>
                <div class="title">
                    <span></span>
                    <p>[单程包车]</p>
                </div>
                <div class="content">
                    <p>包含：车辆使用费、燃油费、路桥费、司机服务费、乘客责任险。</p>
                </div>
            </li>
            <li>
                 <div class="title">
                    <span></span>
