                </div>
                <div class="content">
                    <p>包含8小时100公里；</p>
                    <p>不包含超里程费、超时长费、第三方收费、空驶费。</p>
                    <p>超里程费：当日用车总里程超过100公里后，司机将按1.5/公里收取超里程费。</p>
                    <p>超时长费：当日用车总时长超过10小时后，司机将按50元/小时标准收取超时长费。</p>
                    <p>第三方收费：第三方收取的停车费、高速费、过路费、过桥费由乘客现场自付，票据自行保留。</p>
                    <p>空驶费：当日结束用车地点与出发地点间距离若超过50公里，则超出部分将按照1.5元/公里收取空驶费。当日套餐内公里数未用完，可抵扣空驶公里数。</p>
                </div>
            </li>
            <div class="zhuyi">注：若发现订单产生了额外的费用，请在服务器结束后将费用面付司机。如发现司机乱收费可联系客服核实退还</div>
        </div>
