<template>
    <div class="icon">
        <div class="back" @click="$router.back(-1)">
            <p>取消</p>
            <p class="save">保存</p>
        </div>
        <div class="content">
            <div class="top">
                <router-link to="/changeicon">
                    <li class="list" style="border-bottom:1px solid #F7F7F7;">
                        <p>头像</p>
                        <div class="Img"><img src="../assets/images/my/next.png"></div>
                        <div class="Img"><img src="../assets/images/my/icon.png"></div> 
                    </li>
                </router-link>
                <li class="list" style="border-bottom:1px solid #F7F7F7;">
                    <p>昵称</p>
                    <input type="text" placeholder="请输入" class="inputbox">
                </li>
                <li class="list">
                    <p>真实姓名</p>
                    <input type="text" placeholder="请输入" class="inputbox">
                </li>
            </div>
            <div class="bottom">
                <li class="list">
                    <p>联系方式</p>
                    <span class="phoneFont">{{user.phone}}</span>
                </li>
                <li class="list">
                    <p>收货地址</p>
                    <div class="Img"><img src="../assets/images/my/next.png"></div>
                </li>
                <li class="list">
                    <p>公司名</p>
                    <input type="text" placeholder="请输入" class="inputbox">
                </li>
                <li class="list">
                    <p>职位</p>
                    <input type="text" placeholder="请输入" class="inputbox">
                </li>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            // person:""
        }
    },
    // created() {
    //     this.person=this.$store.getters.user
    //     console.log(this.person)
    // },
    computed: {
        user(){
            return this.$store.getters.user
        }
    },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#F7F7F7')
    },
}
</script>
<style scoped>
.back{
  width:100%;
  height:60px;
  line-height:60px;
  border-bottom:1px solid #F7F7F7;
  position:fixed;
  top:0;
  background:#FFFFFF;
  padding:0 10px;
}
.back p{
margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.save{
    float:right!important;
    color:#409DFD!important;
}
.content{
    /* padding:0 0 0 10px; */
    width:100%;
    margin-top:50px;
    /* border:1px solid red; */
    float:left;
    /* background:#FFFFFF; */
}
.top{
    width:100%;
    float:left;
    /* border:1px solid yellow; */
    background:#FFFFFF;
}
.list{
    width:100%;
    height:60px;
    line-height:60px;
    /* border:1px solid green; */
    float:left;
}
.list p{
    float:left;
    padding-left:10px;
    color:#333333;
}
.Img{
    width:30px;
    float:right;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
.inputbox{
    width:40%;
    float:right;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    overflow:auto;
    background:#FFFFFF;
    border:none;
    font-size:12px;
    text-align:right;
    color:#999999;
}
.bottom{
    float:left;
    margin-top:10px;
    /* background:#FFFFFF; */
     background:#FFFFFF;
}
.phoneFont{
    display:block;
    float:right;
    margin-right:10px;
    font-size:14px;
    color:#999999;
}
</style>


