<template>
    <div class="baoche">

        <div class="bcOne">
    
            <div class="mui-row bcOneCenter">
    
                <div class="mui-col-xs-6">
    
                    <a href="javascript:;" @click="bc=0" :class="{'selectStyle':bc===0}">定制包车</a>
    
                </div>
    
                <div class="mui-col-xs-6">
    
                    <a href="javascript:;" @click="bc=1" :class="{'selectStyle':bc===1}">线路包车</a>
    
                </div>
    
            </div>
    
        </div>
    
        <div class="bcTwo">
    
            <div v-show="bc===0">
    
                <div class="bcOne-one">
    
                    <div class="mui-row bcOneCenter">
    
                        <div class="mui-col-xs-4">
    
                            <a href="javascript:;" @click="distance=0" :class="{'selectLine':distance===0}">往返</a>
    
                        </div>
    
                        <div class="mui-col-xs-4">
    
                            <a href="javascript:;" @click="distance=1" :class="{'selectLine':distance===1}">单程</a>
    
                        </div>
    
                        <div class="mui-col-xs-4">
    
                            <a href="javascript:;" @click="distance=2" :class="{'selectLine':distance===2}">包天</a>
    
                        </div>
    
                    </div>
    
                </div>
    
                <div class="bcThree-one">
    
                    <div v-show="distance===0">
    
                        <div>
    
                            <div class="mui-row bcOneCenter">
    
                                <div class="mui-col-xs-4">
                                    {{city.shortName}}
                                </div>
    
                                <div class="mui-col-xs-4"><img src="../../assets/images/return.png" style="width:30px;"></div>
    
                                <div class="mui-col-xs-4">选择目的地</div>
    
                            </div>
                            <div class="mui-row">
                              <div class="mui-col-xs-8 fontCenter"><p>乘坐人数:<input type="text" value="" style="width:80px;border:none;"></p></div>
                            </div>
                            
    
                        </div>
    
                    </div>
    
                    <div v-show="distance===1">
    
                        <div>
                          <div class="mui-row bcOneCenter">
    
                                <div class="mui-col-xs-4">{{city.shortName}}</div>
    
                                <div class="mui-col-xs-4"><img src="../../assets/images/once.png" style="width:30px;"></div>
    
                                <div class="mui-col-xs-4">选择目的地</div>
    
                            </div>
                            <div class="mui-row">
                              <div class="mui-col-xs-8 fontCenter"><p>乘坐人数:<input type="text" value="" style="width:80px;border:none;"></p></div>
                            </div>
                            
                        </div>
    
                    </div>
    
                    <div v-show="distance===2">
    
                        <div>
                          <div class="mui-row bcOneCenter">
    
                                <div class="mui-col-xs-6"><p>{{city.shortName}}</p></div>
    
                                
    
                                <div class="mui-col-xs-6"><p>用车时间</p></div>
    
                            </div>
                            <div class="mui-row">
                              <div class="mui-col-xs-8 fontCenter"><p>乘坐人数:<input type="text" value="" style="width:80px;border:none;"></p></div>
                            </div>
                            
                        </div>
    
                    </div>
    
                </div>
    
            </div>
    
            <div v-show="bc===1">
    
                <div class="bcOne-two">
    
                    <div class="mui-row bcOneCenter">
    
                        <div class="mui-col-xs-6">
    
                            <a href="javascript:;" @click="jiesong=0" :class="{'selectStyle':jiesong===0}">接送机</a>
    
                        </div>
    
                        <div class="mui-col-xs-6">
    
                            <a href="javascript:;" @click="jiesong=1" :class="{'selectStyle':jiesong===1}">火车站接送</a>
    
                        </div>
    
                    </div>
    
                </div>
    
                <div class="bcThree-two">
    
                    <div v-show="jiesong===0">
    
                        <div>这是是接送机的盒子</div>
    
                    </div>
    
                    <div v-show="jiesong===1">
    
                        <div>这是是火车站接送的盒子</div>
    
                    </div>
    
                </div>
    
            </div>
    
        </div>
        <router-link to="/go"><button class="btn" v-on:click="getvalue()">立即包车</button></router-link>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'Trip',
    data() {
        return {
            bc: 0,
            distance: 0,
            jiesong: 0,
            mudi:"目的地"
        }
    },
    props:["city"],
    methods: {
        getvalue(){
            console.log(this.mudi)
        }
    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.baoche {
    width: 90%;
    /* border: 1px solid gray; */
    border-radius: 1%;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
	background:white;
    border-radius: 10px;
	box-shadow: 2px 2px 10px #888888;


}
a{
    font-size: 16px;
    color: #333333;
    font-weight: 400;
}
/* .bcOne-one a{
  font-size:1px;
  color:#333333;
} */
.bcOne {
    width: 100%;
    height: 50px;
    /* background: red; */
}

.bcOneCenter div {
    text-align: center;
    height: 50px;
    line-height: 50px;
    float:left;
}
.bcOneCenter p{
    float:left;
    margin-left:45px;
}
.fontCenter{
  height:50px;
  line-height:50px;
  text-align:center;
  float:left;
}
.selectStyle{
    /* font-weight: 700; */
    font-style: normal;
    font-size: 16px;
    color: #2594FD;
}
.selectLine{
  font-style: normal;
  font-size: 16px;
  color: #2594FD;
  border-bottom:2px solid #2594FD;
}
.btn{
  background:#2594FD;
  border:none;
  width:260px;
  height:40px;
  color:#FFFEFE;
  border-radius:30px;
  position:relative;
  left:50%;
  transform:translateX(-50%);
  margin-bottom:10px;
}
/* .bcTwoCenter div{
  height:50px;
  line-height:50px;
} */
</style>


