<template>
    <div class="local">
        <div class="back" @click="$router.back(-1)">
            <div class="backImg">
            <img src="../assets/images/justgo/back.png">
            </div>
            <p>选择上车地点</p>
        </div>
        <div class="content">
            <div class="select">
                <div class="onlocal">
                    <select class="selectbox">
                        <option>佛山</option>
                        <option>广州</option>
                        <option>深圳</option>
                    </select>
                    <div class="downImg"><img src="../assets/images/cardown.png"></div>
                </div>
                <div class="search">
                    <div class="searchImg"><img src="../assets/images/search.png"></div>
                    <input type="text" class="searchtext" placeholder="您在哪儿上车" v-model="local">
                    <p>取消</p>
                </div>
            </div>
            <div class="selectlocal">
                <p>附近地点</p>
                <div class="selectall">
                    <li>
                        <div class="dotImg"><img src="../assets/images/dot.png"></div>
                        <div class="right">
                            <p>北约商夏（西南门）</p>
                            <span>南海区下平西路恒昌五金电器批发城对面</span>
                        </div>
                    </li>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            local:"",
            ak:"EAa80a92cfca726c23b0ed9f79a015f5"
        }
    },
    created() {
        this.$axios.get("https://api.map.baidu.com/place/v2/suggestion?query=南海区&region=佛山&city_limit=true&output=json&ak=EAa80a92cfca726c23b0ed9f79a015f5")
        .then((data)=>{
            console.log(data)
        })
    },
    mounted() {
        document.querySelector('body').setAttribute('style', 'background-color:#FFFFFF')
    },
}
</script>
<style scoped>
.back{
  width:100%;
  height:50px;
  line-height:50px;
  /* border:1px solid yellow; */
  background:#FFFFFF;
  padding:0 10px;
  position:fixed;
  top:0px;
  z-index:999999;
}
.back p{
    margin-left:5px;
  float:left;
  color:#333333;
}
img{
  width:100%;
  height:100%;
  display:block;
}
.backImg{
  width:20px;
  position:relative;
  top:50%;
  transform:translateY(-50%);
  float:left;
}
.content{
    margin-top:50px;
    padding:0 10px;
    width:100%;
    float:left;
    /* border:1px solid red; */
}
.select{
    width:100%;
    float:left;
    /* border:1px solid blue; */
}
.onlocal{
    float:left;
    width:20%;
    height:40px;
    overflow:hidden;
    /* border:1px solid red; */
}
.selectbox{
    /* border:1px solid yellow; */
    background:#F5FAFF;
    color:#2596FC;
    height:40px;
    width:100%;
    float:left;
}
.downImg{
    width:20px;
    /* margin-top:12px;
    margin-left:-18px; */
    float:left;
    position:relative;
    top:-45px;
    left:40px;
    
}
.search{
    /* margin-top:10px; */
    width:75%;
    float:right;
    height:30px;
    background:#F7F7F7;
    /* border:1px solid blue; */
    border-radius:30px;
    margin-top:5px;
}
.searchtext{
    /* border:1px solid green; */
    width:180px;
    height:25px;
    position:relative;
    top:50%;
    border:none;
    background:#F7F7F7;
    transform:translateY(-50%);
    font-size:14px;
}
.search p{
    float:right;
    font-size:12px;
    /* border:1px solid red; */
    margin-right:10px;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
.searchImg{
    width:20px;
    margin-left:10px;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
.selectlocal{
    /* border:1px solid blue; */
    width:100%;
    float:left;
}

.selectlocal>p{
    width:100%;
    height:30px;
    line-height:30px;
    /* border:1px solid red; */
    float:left;
    margin-left:10px;
}
.selectall{
    float:left;
    /* border:1px solid yellow; */
    margin-bottom:10px;
}
.dotImg{
    width:30px;
    float:left;
}
.right{
    width:80%;
    float:left;
    margin-left:10px;
    overflow:hidden;
    /* border:1px solid green; */
    /* height: */
}
.right p{
    width:100%;
    float:left;
    height:20px;
    line-height:20px;
    font-size:14px;
    /* border:1px solid red; */
    color:#333333;
}
.right span{
    display:block;
    height:10px;
    line-height:10px;
    width:100%;
    float:left;
    font-size:12px;
    margin-top:-5px;
    color:#999999;

}
</style>


