<template>
  <div class="home">
    <div class="header">
      <div class="mui-row">
         <div class="mui-col-xs-3">
           <router-link to="/location">
            <div class="local">
            <img src="../assets/images/local.png" style="width:30px;">
             <p>{{city.shortName}}</p>
            </div>
            </router-link>
         </div>
         <div class="mui-col-xs-6">
           <div class="title"><img src="../assets/images/title.png" style="width:80px;"></div>
         </div>
         <div class="mui-col-xs-3">
           <div class="kefu">
             <img src="../assets/images/kefu.png" style="width:30px">
           </div>
         </div>
      </div>
    </div>
    <div class="boxscroll">
      <div class="box">
        <div class="banner">
          <img src="../assets/images/banner.jpg">
        </div>
        <div class="route">
          <trip :city="city"></trip>
        </div>
        <div class="partRoute">
          <div class="mui-row">
            <div class="mui-col-xs-3">
              <router-link to="/discountroute">
                <div>
                  <img src="../assets/images/tejia.png">
                  <p>特价专线</p>
                </div>
              </router-link>
            </div>
            <div class="mui-col-xs-3">
              <router-link to="/hotline">
                <div>
                  <img src="../assets/images/hotrouter.png">
                  <p>热门路线</p>
                </div>
              </router-link>
            </div>
            <div class="mui-col-xs-3">
              <router-link to="/selectcar">
              <div>
                <img src="../assets/images/carShow.png">
                <p>车型展示</p>
              </div>
              </router-link>
            </div>
            <div class="mui-col-xs-3">
              <div>
                <img src="../assets/images/sucessDemo.png">
                <p>成功案例</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <carshow></carshow>
      <discountline></discountline>
      <hotroute></hotroute>
      <sucess></sucess>
      <partner></partner>
      
    </div>
      <bar></bar>
  </div>
</template>

<script>
import Trip from '@/components/homeCom/Trip'
import CarTypeShow from '@/components/homeCom/CarTypeShow'
import DiscountLine from '@/components/homeCom/DiscountLine'
import HotRoute from '@/components/homeCom/HotRoute'
import SuccessfulCase from '@/components/homeCom/SuccessfulCase'
import Partner from '@/components/homeCom/Partner'
import bottomBar from '@/components/homeCom/bottomBar'
export default {
  name: 'Home',
  data () {
    return {
      contents:"",
      a:1,
      city:""
    }
  },
  created() {
      if(!localStorage.getItem("city")){
        let storage=window.localStorage
        this.$axios.get("/api/api-whale/area/ipQuery")
        .then((data)=>{
        console.log(data)
        this.city=data.data.data;
        let city=data.data.data;
        let cityValue=JSON.stringify(city)
        storage.setItem('city',cityValue)

      })
    }else{
        let storage=window.localStorage
        let city=storage.getItem('city')
        let cityObj=JSON.parse(city)
        this.city=cityObj
      }
      

    
  },
  components:{
    'trip':Trip,
    'carshow':CarTypeShow,
    'discountline':DiscountLine,
    'hotroute':HotRoute,
    'sucess':SuccessfulCase,
    'partner':Partner,
    'bar':bottomBar
  },
  mounted() {
    document.querySelector('body').setAttribute('style', 'background-color:#F7F7F7')
  },
  methods: {
    getContent:function(){
      this.a+=1;
      console.log(this.contents.length)
    }

  
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.home{
  width:100%;
}
.header{
  width:100%;
  height:50px;
  background-color:#FFFFFF;
  position:fixed;
  top:0px;
  z-index:999999999999;
}
.route{
  position:relative;
  top:-50px;
}

.local{
  height:50px;
  line-height:50px;
  text-align:center;
  padding-left:10px;
}
.local img{
  float:left;
  position:relative;
  top:50%;
  transform:translateY(-50%);
}
.local p{
  margin-left:12px;
  float:left;
  /* font-size:30px; */
  color:rgb(51,51,51);
}
.title{
  height:50px;
  text-align:center;
}
.title img{
  position:relative;
  top:50%;
  transform:translateY(-50%);
}
.kefu{
  height:50px;
  line-height:50px;
  /* border:1px solid blue; */
  text-align:center;
  padding-right:10px;
}
.kefu img{
  float:right;
  position:relative;
  top:50%;
  transform:translateY(-50%);
}

.box{
  width:100%;
  /* border:1px solid red; */
  background-color:#FFFFFF;
  float:left;
  margin-top:-10px;
}
.boxscroll{
  width:100%;
  /* height:600px; */
  float:left;
  margin-top:60px;
  /* border:1px solid red; */
}
.banner{
  width:100%;
  height:180px;
}
.banner img{
  width:100%;
  height:100%;
}
.partRoute{
  margin-top:-20px;
}
.partRoute p{
  text-align:center;
}
.partRoute img{
  position:relative;
  left:50%;
  transform:translateX(-50%);
  width:30px;
}

</style>



