<template>
    <div class="header">
        <div class="slide">
            <Swiper>
            <Slide>
                <div class="carshow">
                    <img :src="imgWeb+car.carImg">
                </div>
            </Slide>
            <Slide>
                <div class="carshow">2</div>
            </Slide>
            <Slide>
                <div class="carshow">
                    <img :src="imgWeb+car.carImg">
                </div>
            </Slide>
            <Slide>
                <div class="carshow">4</div>
            </Slide>
            <Slide>
                <div class="carshow">
                    <img :src="imgWeb+car.carImg">
                </div>
            </Slide>
            </Swiper>
        </div>
        <div class="priceMes">
            <div class="left">
                <p class="car">{{car.name}}</p>
                <p class="place">座位数：{{car.carSeat}}人</p>
            </div>
            <div class="right">
                <button class="btn">立刻包车</button>
            </div>
        </div>
    </div>
</template>
<script>
import { Swiper, Slide } from 'vue-swiper-component';
export default {
    data(){
        return{
            imgWeb:"http://m.whalebus.cn:8006",
        }
    },
    props:["car"],
    components: {
    Swiper,
    Slide
    }
}
</script>
<style scoped>
.header{
    width:100%;
    float:left;
    background:#FFFFFF;
    /* border:1px solid blue; */
}
.swiper{
    width:100%;

    height:310px;
    /* border:1px solid red; */
}

.priceMes{
    width:100%;
    height:80px;
    /* border:1px solid green; */
    padding:0 10px;
    /* border:1px solid red; */
}
.left{

    float:left;
    height:40px;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
.car{
    font-size:16px;
    color:#333333;
    /* border:1px solid red; */
}
.place{
    margin-top:-5px;
    color:#FF2A01;
    /* border:1px solid red; */
    font-weight:500;
}
.right{
    float:right;
    position:relative;
    top:50%;
    transform:translateY(-50%);
}
.btn{
    background:red;
    color:#FFFFFF;
    width:100px;
    height:40px;
}
.carshow{
    width:80%;
    position:relative;
    left:50%;
    transform:translateX(-50%);
    /* height:260px; */
}
img{
    width:100%;
    height:100%;
    
}
</style>


